body {
  margin: 0;
  font-family: -apple-system,Arial, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #f2f2f2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2 {
  color: #333;
  font-size: 36px;
  text-align: center;
}
.success{
  position: fixed;
  top: 10px;
  right: 10px;
  background-color: #4CAF50;
  color: white;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  z-index: 9999;
}
.formDiv {
  background-color: #f5f5f5;

  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
}
input {
  margin-bottom: 10px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #0069d9;
}
.courseItem {
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
}

.courseCode {
  font-weight: bold;
  margin-bottom: 5px;
}

.instructor {
  font-style: italic;
}
.coursedetails{
  background-color: #f5f5f5;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 25px;
}
.courseno{
  font-weight: bold;
  margin-bottom: 5px;
  font-size: 24px;
  color: #333;
}
.details{
  font-style: italic;
  font-size: 18px;
  color: #666;
  margin-top: 10px;
}
.details span{
  font-weight: bold;
  color: #333;
}
.link {
  display: inline-block;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  margin-top: 10px;
  text-decoration: none;
}

.link:hover {
  background-color: #0069d9;
}
.customLink {
  color: #007bff;
  text-decoration: none;
  cursor: pointer;
}